/*******************************************************************************
 * Styles for the base elements of the theme.
 ******************************************************************************/

// Typography

p, h1, h2, h3, h4, h5, h6, em, div, li, span, strong {
 color: var(--global-text-color);
}

p {
 margin-bottom: 1.25rem;
}

hr {
 border-top: 1px solid var(--global-divider-color);
}

table {
 td, th {
 color: var(--global-text-color);
 }
 td {
 font-size: 1rem;
 }
}

a, table.table a {
 color: var(--global-theme-color);
 text-decoration: none;
 transition: color 0.2s ease;
 &:hover {
 color: var(--global-hover-color);
 text-decoration: underline;
 }
 &:hover:after :not(.nav-item.dropdown) {
 width: 100%;
 }
}

figure, img {
 max-width: 90vw;
}

blockquote {
 background: var(--global-bg-color);
 border-left: 3px solid var(--global-theme-color);
 margin: 1.75em 0;
 padding: 0.75em 1.25em;
 font-size: 1.05rem;
 color: var(--global-text-color-light);
}

// Math

.equation {
 margin-bottom: 1rem;
 text-align: center;
}

// Caption

.caption {
 font-size: 0.875rem;
 margin-top: 0.75rem;
 margin-bottom: 1.5rem;
 text-align: center;
}

// Card

.card {
 background-color: var(--global-card-bg-color);
 border: 1px solid var(--global-divider-color);
 border-radius: 10px;
 transition: box-shadow 0.2s ease, transform 0.2s ease;

 &:hover {
 box-shadow: 0 4px 16px rgba(0, 0, 0, 0.06);
 }

 img {
 width: 100%;
 border-radius: 10px 10px 0 0;
 }

 .card-title {
 color: var(--global-text-color);
 }

 .card-item {
 width: auto;
 margin-bottom: 10px;

 .row {
 display: flex;
 align-items: center;
 }
 }
}

// Citation

.citation, .citation-number {
 color: var(--global-theme-color);
}

// Profile (legacy - kept for compatibility)

.profile {
 width: 100%;

 img {
 border-radius: 12px;
 }

 .address {
 margin-bottom: 8px;
 margin-top: 8px;
 font-family: $font-family-mono;
 font-size: 0.8rem;
 color: var(--global-text-color-light);
 p {
 display: inline-block;
 margin: 0;
 }
 }
}
.profile.float-right{
 margin-left: 1.5rem;
}
.profile.float-left{
 margin-right: 1.5rem;
}

@media (min-width: 576px) {
 .profile {
 width: 30%;
 .address {
 p { display: block; }
 }
 }
}

// About Hero (new centered layout)

.about-hero {
 text-align: center;
 padding-top: 2rem;
 padding-bottom: 0.5rem;

 figure {
 margin: 0 auto 1.25rem;
 display: inline-block;
 }

 .about-hero-img {
 width: 150px;
 height: 150px;
 border-radius: 50%;
 object-fit: cover;
 box-shadow: 0 2px 16px rgba(0, 0, 0, 0.08);
 }
}

.about-hero-name {
 font-size: 2rem;
 font-weight: 700;
 letter-spacing: -0.03em;
 margin-bottom: 0.25rem;
}

.about-hero-desc {
 font-size: 1rem;
 color: var(--global-text-color-light);
 margin-bottom: 0.4rem;
}

.about-hero-location {
 font-family: $font-family-mono;
 font-size: 0.8rem;
 color: var(--global-text-color-light);
 margin-bottom: 0.75rem;
 p {
 display: inline;
 margin: 0;
 }
}

.about-hero-social {
 margin-top: 0.5rem;
 font-size: 1.3rem;
 a {
 margin: 0 0.35rem;
 i::before {
 color: var(--global-text-color-light);
 transition: color 0.2s ease;
 }
 &:hover {
 text-decoration: none;
 i::before {
 color: var(--global-theme-color);
 }
 }
 }
}

.about-divider {
 margin: 1.75rem 0;
 border-top: 1px solid var(--global-divider-color);
}

.about-content {
 font-size: 1.02rem;
 line-height: 1.75;
}

.post-description {
 margin-bottom: 2rem;
 font-size: 0.9rem;
 color: var(--global-text-color-light);
 a {
 color: inherit;
 &:hover {
 color: var(--global-theme-color);
 text-decoration: none;
 }
 }
}


// Navbar customization

.navbar {
 box-shadow: none;
 border-bottom: 1px solid var(--global-divider-color);
 background-color: var(--global-bg-color);
 opacity: 0.98;
 backdrop-filter: blur(10px);
 -webkit-backdrop-filter: blur(10px);
 padding: 0.6rem 0;
 transition: all 0.3s ease;

 @media (max-width: 767px) {
 padding-left: 1rem;
 padding-right: 1rem;
 }

 a {
 &:hover {
 text-decoration: none;
 }
 }
 .navbar-brand {
 color: var(--global-text-color);
 font-weight: 600;
 letter-spacing: -0.01em;
 }
 .navbar-nav .nav-item .nav-link {
 color: var(--global-text-color);
 font-size: 0.9rem;
 font-weight: 450;
 letter-spacing: 0.01em;
 padding: 0.5rem 0.85rem;
 transition: color 0.2s ease;
 &:hover {
 color: var(--global-hover-color);
 }
 }
 .navbar-nav .nav-item.active>.nav-link {
 background-color: inherit;
 font-weight: 600;
 color: var(--global-theme-color);
 &:hover {
 color: var(--global-hover-color);
 }
 }
 .navbar-brand.social {
 padding-bottom: 0;
 padding-top: 0;
 font-size: 1.5rem;
 a {
 i::before {
 color: var(--global-text-color-light);
 transition: all 0.2s ease-in-out;
 }
 &:hover {
 i::before {
 color: var(--global-theme-color);
 }
 }
 }
 }
}
.navbar .dropdown-menu {
 background-color: var(--global-bg-color);
 border: 1px solid var(--global-divider-color);
 border-radius: 8px;
 box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
 padding: 0.4rem 0;
 min-width: 10rem;
 margin-top: 0.35rem;
 a:not(.active) {
 color: var(--global-text-color);
 }
 a:hover {
 color: var(--global-hover-color);
 }
 .dropdown-divider {
 border-top: 1px solid var(--global-divider-color) !important;
 margin: 0.25rem 0;
 }
}
.dropdown-item {
 color: var(--global-text-color);
 font-size: 0.85rem;
 padding: 0.4rem 1rem;
 transition: all 0.15s ease;
 &:hover {
 color: var(--global-theme-color);
 background-color: transparent;
 }
}

.navbar-toggler {
 border: none;
 padding: 0.25rem 0.5rem;
 &:focus {
 box-shadow: none;
 outline: none;
 }
 .icon-bar {
 display: block;
 width: 22px;
 height: 2px;
 background-color: var(--global-text-color);
 border-radius: 1px;
 margin-bottom: 4px;
 transition: all 0.2s;
 }
 .top-bar {
 transform: rotate(45deg);
 transform-origin: 10% 10%;
 }
 .middle-bar {
 opacity: 0;
 }
 .bottom-bar {
 transform: rotate(-45deg);
 transform-origin: 10% 90%;
 }
}

.navbar-toggler.collapsed {
 .top-bar {
 transform: rotate(0);
 }
 .middle-bar {
 opacity: 1;
 }
 .bottom-bar {
 transform: rotate(0);
 }
}

.toggle-container {
 display: flex;
 align-items: center;
}

#light-toggle {
 padding: 0.5rem 0.85rem;
 border: none;
 background-color: inherit;
 color: var(--global-text-color-light);
 cursor: pointer;
 font-size: 0.9rem;
 line-height: 1;
 transition: color 0.2s ease;
 &:focus {
 box-shadow: none;
 outline: none;
 }
 &:hover {
 color: var(--global-theme-color);
 }
}

// Social (bottom)

.social {
 text-align: center;
 .contact-icons {
 font-size: 3rem;
 a {
 i::before {
 color: var(--global-text-color-light);
 transition: all 0.2s ease-in-out;
 }
 &:hover {
 i::before {
 color: var(--global-theme-color);
 }
 }
 }
 }
 .contact-note {
 font-size: 0.8rem;
 color: var(--global-text-color-light);
 }
}


// Footer
footer.fixed-bottom {
 background-color: var(--global-bg-color);
 border-top: 1px solid var(--global-divider-color);
 font-size: 0.8rem;
 .container {
 color: var(--global-text-color-light);
 padding-top: 14px;
 padding-bottom: 14px;
 text-align: center;
 }
 a {
 color: var(--global-text-color-light);
 &:hover {
 color: var(--global-theme-color);
 text-decoration: none;
 }
 }
}

footer.sticky-bottom {
 border-top: 1px solid var(--global-divider-color);
 padding-top: 40px;
 padding-bottom: 40px;
 font-size: 0.85rem;
 color: var(--global-text-color-light);
 text-align: center;
}

// CV

.cv-downloads {
 display: flex;
 gap: 0.75rem;
 margin-bottom: 2rem;

 .cv-download-btn {
 display: inline-flex;
 align-items: center;
 gap: 0.4rem;
 color: var(--global-theme-color);
 border: 1px solid var(--global-theme-color);
 padding: 0.4rem 1rem;
 border-radius: 6px;
 font-size: 0.82rem;
 font-weight: 600;
 text-decoration: none;
 transition: all 0.15s ease;

 &:hover {
 background-color: var(--global-theme-color);
 color: white;
 text-decoration: none;
 }

 i {
 font-size: 0.9rem;
 }
 }
}

.cv {
 margin-bottom: 2rem;
}

.cv-section {
 margin-bottom: 1.75rem;

 .cv-section-title {
 color: var(--global-text-color-light);
 margin-bottom: 0.75rem;
 text-align: left;
 font-size: 0.8rem;
 font-weight: 600;
 text-transform: uppercase;
 letter-spacing: 0.1em;
 border: none;
 }

 .cv-section-content {
 padding-left: 0;
 }
}

// Time table entries (education, experience)
.cv-entry {
 padding: 0.7rem 0;
 border-bottom: 1px solid var(--global-divider-color);

 &:last-child {
 border-bottom: none;
 }

 .cv-entry-header {
 display: flex;
 align-items: baseline;
 gap: 1rem;
 }

 .cv-entry-year {
 flex-shrink: 0;
 font-size: 0.78rem;
 font-weight: 600;
 color: var(--global-theme-color);
 min-width: 100px;
 }

 .cv-entry-info {
 display: flex;
 flex-direction: column;
 gap: 0.1rem;
 }

 .cv-entry-title {
 font-weight: 700;
 font-size: 0.92rem;
 color: var(--global-text-color);
 }

 .cv-entry-institution {
 font-size: 0.84rem;
 color: var(--global-theme-color);
 font-weight: 500;

 a {
 color: inherit;
 text-decoration: none;
 border-bottom: 1px dotted var(--global-theme-color);

 &:hover {
 border-bottom-style: solid;
 }
 }
 }

 .cv-entry-desc {
 margin: 0.35rem 0 0 0;
 padding-left: 116px; // align with info (100px year + 1rem gap)
 list-style: none;

 li {
 font-size: 0.82rem;
 color: var(--global-text-color-light);
 line-height: 1.6;
 padding-left: 0.85rem;
 padding-bottom: 0.2rem;
 position: relative;

 &::before {
 content: "·";
 position: absolute;
 left: 0;
 color: var(--global-text-color-light);
 }

 strong {
 color: var(--global-text-color);
 font-weight: 600;
 }
 }
 }

 @media (max-width: 576px) {
 .cv-entry-header {
 flex-direction: column;
 gap: 0.15rem;
 }

 .cv-entry-year {
 min-width: auto;
 }

 .cv-entry-desc {
 padding-left: 0.85rem;
 }
 }
}

// Simple list
.cv-list {
 list-style: none;
 padding: 0;
 margin: 0;

 li {
 font-size: 0.88rem;
 color: var(--global-text-color);
 padding: 0.4rem 0;
 border-bottom: 1px solid var(--global-divider-color);
 line-height: 1.5;

 &:last-child {
 border-bottom: none;
 }

 a {
 color: var(--global-theme-color);
 text-decoration: none;
 &:hover {
 text-decoration: underline;
 }
 }
 }
}

// Skill groups (clean table layout)
.cv-skill-group {
 display: flex;
 gap: 1rem;
 padding: 0.45rem 0;
 border-bottom: 1px solid var(--global-divider-color);

 &:last-child {
 border-bottom: none;
 }

 .cv-skill-label {
 flex-shrink: 0;
 min-width: 130px;
 font-weight: 600;
 font-size: 0.82rem;
 color: var(--global-text-color);
 }

 .cv-skill-tags {
 font-size: 0.82rem;
 color: var(--global-text-color-light);
 line-height: 1.6;
 }

 .cv-skill-tag {
 &:not(:last-child)::after {
 content: " · ";
 color: var(--global-divider-color);
 }
 }

 @media (max-width: 576px) {
 flex-direction: column;
 gap: 0.15rem;

 .cv-skill-label {
 min-width: auto;
 }
 }
}

// Map (key-value pairs)
.cv-map {
 margin: 0;

 .cv-map-row {
 display: flex;
 gap: 1rem;
 padding: 0.35rem 0;
 border-bottom: 1px solid var(--global-divider-color);

 &:last-child {
 border-bottom: none;
 }

 dt {
 flex-shrink: 0;
 min-width: 120px;
 font-weight: 600;
 font-size: 0.88rem;
 color: var(--global-text-color);
 }

 dd {
 font-size: 0.88rem;
 color: var(--global-text-color-light);
 margin: 0;
 }
 }
}

// Repositories

.repo-grid {
 display: grid;
 grid-template-columns: repeat(3, 1fr);
 gap: 1.25rem;
 margin-bottom: 2rem;

 @media (max-width: 768px) {
 grid-template-columns: repeat(2, 1fr);
 }

 @media (max-width: 480px) {
 grid-template-columns: 1fr;
 }
}

.repo-card {
 display: flex;

 a {
 text-decoration: none;
 color: inherit;
 display: flex;
 width: 100%;
 &:hover {
 text-decoration: none;
 }
 }

 .repo-card-inner {
 padding: 1.25rem;
 border: 1px solid var(--global-divider-color);
 border-radius: 10px;
 background-color: var(--global-card-bg-color);
 transition: border-color 0.2s ease, box-shadow 0.2s ease;
 width: 100%;
 display: flex;
 flex-direction: column;

 &:hover {
 border-color: var(--global-theme-color);
 box-shadow: 0 2px 12px rgba(0, 0, 0, 0.06);
 }
 }

 .repo-card-header {
 display: flex;
 align-items: center;
 gap: 0.5rem;
 margin-bottom: 0.5rem;
 }

 .repo-card-icon {
 font-size: 1.15rem;
 color: var(--global-text-color-light);
 }

 .repo-card-name {
 font-size: 0.95rem;
 font-weight: 600;
 color: var(--global-theme-color);
 }

 .repo-card-desc {
 font-size: 0.85rem;
 color: var(--global-text-color-light);
 margin: 0 0 0.75rem 0;
 line-height: 1.5;
 flex-grow: 1;
 }

 .repo-card-meta {
 display: flex;
 align-items: center;
 gap: 1rem;
 font-size: 0.78rem;
 color: var(--global-text-color-light);
 margin-top: auto;
 padding-top: 0.5rem;
 }

 .repo-card-lang {
 display: flex;
 align-items: center;
 gap: 0.3rem;
 }

 .repo-card-lang-dot {
 width: 8px;
 height: 8px;
 border-radius: 50%;
 background-color: var(--global-theme-color);
 display: inline-block;
 }

 .repo-card-stars {
 i {
 font-size: 0.7rem;
 }
 }
}

// Blog

.header-bar {
 border-bottom: 1px solid var(--global-divider-color);
 text-align: center;
 padding-top: 2.5rem;
 padding-bottom: 2.5rem;
 h1 {
 color: var(--global-text-color);
 font-size: 2.5rem;
 font-weight: 700;
 letter-spacing: -0.03em;
 }
 h2 {
 color: var(--global-text-color-light);
 font-size: 1.05rem;
 font-weight: 400;
 margin-top: 0.5rem;
 }
}

.blog-search {
 padding: 1rem 0;
 display: flex;
 justify-content: center;

 .blog-search-wrapper {
 position: relative;
 width: 100%;
 max-width: 400px;
 }

 .blog-search-icon {
 position: absolute;
 left: 0.85rem;
 top: 50%;
 transform: translateY(-50%);
 color: var(--global-text-color-light);
 font-size: 0.85rem;
 pointer-events: none;
 }

 #blog-search-input {
 width: 100%;
 padding: 0.55rem 0.85rem 0.55rem 2.25rem;
 font-size: 0.9rem;
 font-family: inherit;
 color: var(--global-text-color);
 background-color: var(--global-card-bg-color);
 border: 1px solid var(--global-divider-color);
 border-radius: 8px;
 outline: none;
 transition: border-color 0.2s ease, box-shadow 0.2s ease;

 &::placeholder {
 color: var(--global-text-color-light);
 }

 &:focus {
 border-color: var(--global-theme-color);
 box-shadow: 0 0 0 3px rgba(var(--global-theme-color-rgb, 104, 95, 239), 0.1);
 }
 }
}

.blog-show-more {
 text-align: center;
 padding: 1.5rem 0;

 .btn {
 color: var(--global-text-color-light);
 border: 1px solid var(--global-divider-color);
 padding: 0.45rem 1.5rem;
 font-size: 0.85rem;
 border-radius: 6px;
 background: var(--global-card-bg-color);
 cursor: pointer;
 transition: all 0.15s ease;

 &:hover {
 color: var(--global-theme-color);
 border-color: var(--global-theme-color);
 }
 }
}

.post-list {
 margin: 0;
 margin-bottom: 40px;
 padding: 0;
 li {
 border-bottom: 1px solid var(--global-divider-color);
 list-style: none;
 padding-top: 1.75rem;
 padding-bottom: 1.75rem;
 transition: padding-left 0.2s ease;
 &:hover {
 padding-left: 0.25rem;
 }
 h3 {
 margin-bottom: 0.4rem;
 font-size: 1.2rem;
 }
 p {
 color: var(--global-text-color-light);
 font-size: 0.95rem;
 margin-bottom: 0.4rem;
 }
 .post-meta {
 color: var(--global-text-color-light);
 font-size: 0.8rem;
 margin-bottom: 0;
 }
 .post-tags {
 color: var(--global-text-color-light);
 font-size: 0.8rem;
 padding-top: 0.25rem;
 padding-bottom: 0;
 }
 a {
 color: var(--global-text-color);
 text-decoration: none;
 &:hover {
 color: var(--global-theme-color);
 }
 }
 }
}

.pagination {
 .page-item {
 .page-link {
 color: var(--global-text-color);
 background-color: var(--global-bg-color);
 border: 1px solid var(--global-divider-color);
 border-radius: 6px;
 margin: 0 2px;
 padding: 0.4rem 0.85rem;
 font-size: 0.85rem;
 transition: all 0.2s ease;
 &:hover {
 color: var(--global-theme-color);
 border-color: var(--global-theme-color);
 background-color: var(--global-bg-color);
 }
 }
 &.active .page-link {
 color: #fff;
 background-color: var(--global-theme-color);
 border-color: var(--global-theme-color);
 &:hover {
 background-color: var(--global-theme-color);
 }
 }
 &.disabled .page-link {
 color: var(--global-text-color-light);
 opacity: 0.5;
 }
 }
}


// Distill

.distill {
 a:hover {
 border-bottom-color: var(--global-theme-color);
 text-decoration: none;
 }
}


// Teaching

.teaching {
 .section-heading {
 color: var(--global-text-color-light);
 margin-top: 2rem;
 margin-bottom: 0.75rem;
 text-align: left;
 font-size: 0.8rem;
 font-weight: 600;
 text-transform: uppercase;
 letter-spacing: 0.1em;
 border: none;
 }
}

.teaching-list {
 display: flex;
 flex-direction: column;
}

.teaching-item {
 padding: 0.85rem 0;
 border-bottom: 1px solid var(--global-divider-color);

 &:last-child {
 border-bottom: none;
 }

 .teaching-item-title {
 font-weight: 600;
 font-size: 0.95rem;
 color: var(--global-text-color);

 a {
 color: var(--global-theme-color);
 text-decoration: none;
 &:hover {
 text-decoration: underline;
 }
 }
 }

 .teaching-item-meta {
 font-size: 0.82rem;
 color: var(--global-text-color-light);
 margin-top: 0.15rem;
 }

 .teaching-item-desc {
 font-size: 0.82rem;
 color: var(--global-text-color-light);
 margin-top: 0.25rem;
 line-height: 1.5;
 }
}

// Projects

.projects {
 h2.category {
 color: var(--global-text-color-light);
 margin-top: 2.5rem;
 margin-bottom: 0.75rem;
 text-align: left;
 font-size: 0.8rem;
 font-weight: 600;
 text-transform: uppercase;
 letter-spacing: 0.1em;
 border: none;
 }
}

.project-grid {
 display: grid;
 grid-template-columns: repeat(2, 1fr);
 gap: 1.25rem;

 @media (max-width: 576px) {
 grid-template-columns: 1fr;
 }
}

.project-card {
 display: flex;

 a {
 text-decoration: none;
 color: inherit;
 display: flex;
 width: 100%;
 &:hover {
 text-decoration: none;
 }
 }

 .project-card-inner {
 border: 1px solid var(--global-divider-color);
 border-radius: 10px;
 background-color: var(--global-card-bg-color);
 overflow: hidden;
 width: 100%;
 display: flex;
 flex-direction: column;
 transition: border-color 0.2s ease, box-shadow 0.2s ease;

 &:hover {
 border-color: var(--global-theme-color);
 box-shadow: 0 2px 12px rgba(0, 0, 0, 0.06);
 }
 }

 .project-card-img {
 width: 100%;
 aspect-ratio: 16 / 9;
 overflow: hidden;

 img {
 width: 100%;
 height: 100%;
 object-fit: cover;
 display: block;
 }
 }

 .project-card-body {
 padding: 1rem 1.25rem;
 flex: 1;
 display: flex;
 flex-direction: column;
 }

 .project-card-title {
 font-size: 0.95rem;
 font-weight: 700;
 color: var(--global-text-color);
 margin-bottom: 0.3rem;
 }

 .project-card-desc {
 font-size: 0.85rem;
 color: var(--global-text-color-light);
 line-height: 1.5;
 margin: 0;
 flex-grow: 1;
 }
}


// Publications

.publications {
 margin-top: 2rem;

 h1 {
 color: var(--global-text-color);
 font-size: 2rem;
 text-align: center;
 margin-top: 1em;
 margin-bottom: 1em;
 }

 h2, h2.year, h2.bibliography {
 color: var(--global-text-color-light);
 padding-top: 0;
 margin-top: 2.5rem;
 margin-bottom: 0.75rem;
 text-align: left;
 font-size: 0.8rem;
 font-weight: 600;
 text-transform: uppercase;
 letter-spacing: 0.1em;
 border: none;
 }

 ol.bibliography {
 list-style: none;
 padding: 0;
 margin-top: 0;
 li {
 margin-bottom: 0;
 }
 }
}

// Publication entry card
.pub-entry {
 padding: 1rem 0;
 border-bottom: 1px solid var(--global-divider-color);

 &:last-child {
 border-bottom: none;
 }

 .pub-content {
 .title {
 font-weight: 800;
 font-size: 0.95rem;
 line-height: 1.5;
 margin-bottom: 0.35rem;
 color: var(--global-text-color);
 }

 .author {
 font-size: 0.84rem;
 color: var(--global-text-color-light);
 margin-bottom: 0.3rem;
 line-height: 1.5;

 a {
 color: var(--global-theme-color);
 text-decoration: none;
 border-bottom: 1px dashed var(--global-theme-color);
 &:hover {
 border-bottom-style: solid;
 text-decoration: none;
 }
 }

 > em {
 border-bottom: 1px solid;
 font-style: normal;
 }

 > span.more-authors {
 color: var(--global-text-color-light);
 border-bottom: 1px dashed var(--global-text-color-light);
 cursor: pointer;
 &:hover {
 color: var(--global-text-color);
 border-bottom: 1px dashed var(--global-text-color);
 }
 }
 }

 .periodical {
 font-size: 0.82rem;
 color: var(--global-text-color-light);
 margin-bottom: 0.4rem;

 em {
 font-style: italic;
 }

 .periodical-year {
 &::before {
 content: " · ";
 }
 }

 .periodical-status {
 font-style: italic;
 }
 }

 .links {
 display: flex;
 flex-wrap: wrap;
 gap: 0.15rem 0.6rem;
 margin-top: 0.35rem;

 a.btn {
 color: var(--global-theme-color) !important;
 background: none !important;
 border: 1px solid var(--global-theme-color) !important;
 padding: 0.15rem 0.45rem !important;
 font-size: 0.6rem;
 font-weight: 600;
 text-transform: uppercase;
 letter-spacing: 0.03em;
 border-radius: 4px;
 transition: all 0.15s ease;
 box-shadow: none !important;

 &:hover {
 background: var(--global-theme-color) !important;
 color: white !important;
 }
 }
 }

 .hidden {
 font-size: 0.85rem;
 max-height: 0;
 overflow: hidden;
 text-align: justify;
 transition: all 0.15s ease;

 p {
 line-height: 1.5;
 margin: 0.75rem 0;
 }
 pre {
 font-size: 0.85rem;
 line-height: 1.4;
 padding: 0.75rem;
 }
 }

 .hidden.open {
 max-height: 100em;
 transition: all 0.15s ease;
 }

 div.abstract.hidden {
 border: dashed 1px var(--global-bg-color);
 }

 div.abstract.hidden.open {
 border-color: var(--global-text-color);
 }
 }
}

// Code blocks & syntax highlighting
figure.highlight {
 margin: 0 0 1.25rem;
}

pre {
 color: var(--global-text-color);
 background-color: var(--global-code-bg-color);
 border-radius: 8px;
 padding: 1rem 1.25rem;
 font-family: $font-family-mono;
 font-size: 0.84rem;
 line-height: 1.7;
 border: 1px solid var(--global-divider-color);
 overflow-x: auto;

 pre, code {
 background-color: transparent;
 border-radius: 0;
 border: none;
 padding: 0;
 color: inherit;
 font-size: inherit;
 }
}

code {
 color: var(--global-theme-color);
 background-color: var(--global-code-bg-color);
 border-radius: 4px;
 padding: 2px 6px;
 font-family: $font-family-mono;
 font-size: 0.84em;
}

// Inline code inside headings & links should not have bg
h1, h2, h3, h4, h5, h6 {
 code {
 font-size: 0.85em;
 }
}

// Plaintext output blocks (no syntax highlighting)
.highlighter-rouge {
 .highlight {
 pre.highlight {
 background-color: var(--global-code-bg-color);
 color: var(--global-text-color);
 }
 }
}


// Transitioning Themes
html.transition,
html.transition *,
html.transition *:before,
html.transition *:after {
 transition: all 500ms ease !important;
 transition-delay: 0 !important;
}

// Extra Markdown style (post Customization)
.post{
 .post-meta{
 color: var(--global-text-color-light);
 font-size: 0.85rem;
 margin-bottom: 0;
 }
 .post-tags{
 color: var(--global-text-color-light);
 font-size: 0.85rem;
 padding-top: 0.25rem;
 padding-bottom: 1rem;
 a {
 color: var(--global-text-color-light);
 text-decoration: none;
 transition: color 0.2s ease;
 &:hover {
 color: var(--global-theme-color);
 }
 }
 }
 .post-content{
 blockquote {
 border-left: 3px solid var(--global-theme-color);
 padding: 0.5rem 1rem;
 color: var(--global-text-color-light);
 }

 h2 {
 margin-top: 2.5rem;
 margin-bottom: 1rem;
 }

 h3 {
 margin-top: 2rem;
 margin-bottom: 0.75rem;
 }
 }
}

progress {
 /* Positioning */
 position: fixed;
 left: 0;
 top: 56px;
 z-index: 10;
 
 /* Dimensions */
 width: 100%;
 height: 1px;
 
 /* Reset the appearance */
 -webkit-appearance: none;
 -moz-appearance: none;
 appearance: none;
 
 /* Get rid of the default border in Firefox/Opera. */
 border: none;
 
 /* Progress bar container for Firefox/IE10 */
 background-color: transparent;
 
 /* Progress bar value for IE10 */
 color: var(--global-theme-color);
 }
 
 progress::-webkit-progress-bar {
 background-color: transparent;
 }
 
 progress::-webkit-progress-value {
 background-color: var(--global-theme-color);
 }
 
 progress::-moz-progress-bar {
 background-color: var(--global-theme-color);
 }
 
 .progress-container {
 width: 100%;
 background-color: transparent;
 position: fixed;
 top: 56px;
 left: 0;
 height: 5px;
 display: block;
 }
 
 .progress-bar {
 background-color: var(--global-theme-color);
 width: 0%;
 display: block;
 height: inherit;
 }
